<template>
  <div class="index">
    <div class="login">
      <img id="avatar" src="../assets/img/avatar.jpg" alt="">

      <div class="box">
        <div class="title">
          <h3>
            <p>Welcome to IO!</p>

            <p>Please Sign-in to your account.</p>

          </h3>
        </div>
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item prop="username">
            <el-input v-model="form.username" placeholder="请输入昵称" maxlength="12" show-word-limit></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input v-model="form.password" placeholder="请输入密码" maxlength="30" type="password"
                      show-word-limit></el-input>
          </el-form-item>
          <!--          <el-form-item label="邮箱" prop="email">
                      <el-input v-model="form.email" placeholder="请输入邮箱" maxlength="255" show-word-limit></el-input>
                    </el-form-item>-->
          <el-row style="position: relative; display:flex; margin: 0 auto; justify-content: center">

            <el-button class="btn" type="success" @click="onLogin()" plain>登录</el-button>
            <el-button class="btn" type="primary" @click="onRegister()" plain>注册</el-button>


          </el-row>
        </el-form>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      title: '登录',
      form: {
        // id: null,
        username: 'admin',
        password: '123456',
        // email: ''
      },
      rules: {
        username: [
          {required: true, message: '请输入昵称', trigger: 'blur'},
          {min: 2, max: 12, message: '长度在 2 到 12 个字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, message: '请输入正确密码', trigger: 'blur'}
        ],
        email: [
          {required: false, message: '请输入Email', trigger: 'blur'},
          {message: '请输入正确的邮箱地址', trigger: 'blur'}
        ],
      },
    }
  },
  methods: {
    async onLogin() {
      console.log(this.form)
      // 表单验证
      this.$refs['form'].validate(async (valid) => {
        if (!valid) {
          this.$notify.error({
            title: '错误',
            message: '请输入正确信息'
          });
          return
        } else {  //成功
          const {data: res} = await this.$axios.post('/login', this.form)
          if (res.msg === 'login success') {
            this.$message.success(res.msg)
            this.$router.push("/admin")
          } else {
            this.$message.error(res.msg)
          }
        }
      })
    },
    onRegister() {
      this.$router.push('/Register')
    }
  }
}
</script>

